.lds-page{
    padding: 6.1rem 0.8rem;
    max-width: 1280px;
    margin: auto;
    img {
        height: auto;
        max-width: 100%;
    }
    &__title{
        margin: 6.1rem 0;
    }
   h1{
        margin-bottom: 6.1rem!important;
    }
    h2,h3,h4,h5,h6{
          text-align: unset;
         margin: 1.6rem 0;
    }
 
    h2{
        font-size: 2.5rem;
       
    
    }
    h3{
        font-size: 2rem;
       
    }
    p{
        font-size: 1.8rem;
        margin-bottom: 1.6rem;
    }
article{
    padding: 6.1rem 0 ;
}
}